import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: 'home',
    loadChildren: () => import('./home/home.module').then(m => m.homePageModule)
  },
  {
    path: 'confi',
    loadChildren: () => import('./confi/confi.module').then(m => m.ConfiPageModule)
  },
  {
    path: 'Asignacion',
    loadChildren: () => import('./Asignacion/Asignacion.module').then(m => m.AsignacionPageModule)
  },
  {
    path: 'Servicios',
    loadChildren: () => import('./Servicios/Servicios.module').then(m => m.ServiciosPageModule)
  },
  {
    path: 'Agendar',
    loadChildren: () => import('./Agendar/Agendar.module').then(m => m.AgendarPageModule)
  },
  {
    path: 'lista',
    loadChildren: () => import('./lista/lista.module').then(m => m.listaPageModule)
  },
  {
    path: 'login',
    loadChildren: () => import('./login/login.module').then(m => m.LoginPageModule)
  },
  {
    path: 'Registro',
    loadChildren: () => import('./registro/registro.module').then(m => m.RegistroPageModule)
  },
  {
    path: 'loading',
    loadChildren: () => import('./loading/loading.module').then(m => m.LoadingPageModule)
  },


];
@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
