import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LocalStorageServiceService {

  constructor() { }

  GuardarItem(Item: any, Datos: any) {
    localStorage.setItem(Item, Datos)
  }

  BuscarItem(Item: any) {
    return localStorage.getItem(Item)
  }

  BorrarItem(Item: any) {
    localStorage.removeItem(Item)
  }

  RemoverItems() {
    localStorage.clear()
  }

}
